* {
  font-family: Helvetica,Arial, sans-serif;
}
 

input {
  color:grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  align-items: center;
  border: 1px solid grey;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p { 
  text-align: center;
  max-width: 170px;
}

.card img {
  max-height: 170px;
}